<template>
  <div class="dashboard-main-container mt-24 mt-lg-31" id="dashboard-body">
    <div class="container">
      <!-- profile container Begins -->
      <div class="mb-15 mb-lg-23">
        <div class="row">
          <div class="col-xxxl-9 px-lg-13 px-6">
            <h5 class="font-size-6 font-weight-semibold mb-11">My Profile</h5>
            <div
              class="contact-form bg-white shadow-8 rounded-4 pl-sm-10 pl-4 pr-sm-11 pr-4 pt-15 pb-13"
            >
              <div class="upload-file mb-16 text-center">
                <div id="userActions" class="square-144 m-auto px-6 mb-7">
                  <label for="fileUpload" class="mb-0 font-size-4 text-smoke"
                    >Browse or Drag and Drop</label
                  >
                  <input type="file" id="fileUpload" class="sr-only" />
                </div>
                <!-- <img id="imgPrime" src="" alt="uploaded image placeholder" /> -->
                <!-- <input type="file" id="upfile"> -->
              </div>
              <form>
                <fieldset>
                  <div class="row mb-xl-1 mb-9">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="namedash"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Company Name</label
                        >
                        <input
                          v-model="profile.company_name"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. Google"
                        />
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Industry</label
                        >
                        <select
                          id="select2"
                          class="form-control nice-select pl-6 arrow-3 h-px-48 w-100 font-size-4"
                          :disabled="!(profile.industry && industries.length)"
                          @input="setIndustry($event.target.value)"
                          v-model="profile.industry"
                        >
                          <option
                            v-for="c in industries"
                            :value="c._id"
                            :key="c._id"
                            :selected="c._id == profile.industry"
                            >{{ c.name }}</option
                          >
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="row mb-8">
                    <div class="col-lg-6 mb-xl-0 mb-7">
                      <div class="form-group position-relative">
                        <label
                          for="select3"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Employee Size
                        </label>
                        <select
                          id="select3"
                          class="form-control nice-select pl-6 arrow-3 h-px-48 w-100 font-size-4"
                          v-model="profile.employeeSize"
                        >
                          <option>0-50</option>
                          <option>51-100</option>
                          <option>101-500</option>
                          <option>501-1000</option>
                          <option>Above 1000</option>
                        </select>
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group position-relative">
                        <label
                          for="address"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Year Established</label
                        >
                        <b-form-input
                          v-model="$v.profile.yearCreated.$model"
                          :state="validateState($v.profile.yearCreated)"
                          id="feedback-user"
                          placeholder="eg. 1991"
                        ></b-form-input>
                        <b-form-invalid-feedback>
                          Not a valid year format
                        </b-form-invalid-feedback>
                      </div>
                    </div>
                    <div class="col-lg-12 mb-xl-0 mb-7">
                      <div class="form-group position-relative">
                        <label
                          for="select3"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Categories
                        </label>
                        <select
                          id="select2"
                          class="form-control nice-select pl-6 arrow-3 h-px-48 w-100 font-size-4"
                          :disabled="!(profile.category && categories.length)"
                          @input="setCategory($event.target.value)"
                        >
                          v-model="profile.category" >
                          <option
                            v-for="cat in categories"
                            :value="cat._id"
                            :key="cat.name"
                            :selected="cat._id == profile.category"
                            >{{ cat.name }}</option
                          >
                        </select>
                        <!-- {{category == cat.name}} -->
                      </div>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-md-12">
                      <div class="form-group">
                        <label
                          for="aboutTextarea"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >About Company</label
                        >
                        <textarea
                          v-model="profile.about"
                          id="aboutTextarea"
                          cols="30"
                          rows="7"
                          class="border border-mercury text-gray w-100 pt-4 pl-6"
                          placeholder="Describe about the company what make it unique"
                        ></textarea>
                      </div>
                    </div>
                    <div class="col-md-12">
                      <input
                        type="submit"
                        value="Save Profile"
                        class="btn btn-green btn-h-60 text-white min-width-px-210 rounded-5 text-uppercase"
                      />
                    </div>
                  </div>
                </fieldset>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- profile container Begins  -->

    <div class="container">
      <!-- socials container Begins -->
      <div class="mb-15 mb-lg-23">
        <div class="row">
          <div class="col-xxxl-9 px-lg-13 px-6">
            <h5 class="font-size-6 font-weight-semibold mb-11">Socials</h5>
            <div
              class="contact-form bg-white shadow-8 rounded-4 pl-sm-10 pl-4 pr-sm-11 pr-4 pt-15 pb-13"
            >
              <form @submit.prevent="onSubmit">
                <fieldset>
                  <div class="row mb-xl-1 mb-9">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="namedash"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Facebook</label
                        >
                        <input
                          v-model="profile.facebook"
                          type="text"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. www.facebook.com/sieben"
                        />
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Twitter</label
                        >
                        <input
                          type="text"
                          v-model="profile.twitter"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. www.twitter.com/sieben"
                        />
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >LinkedIn</label
                        >
                        <input
                          type="text"
                          v-model="profile.linkedln"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. www.linkedin.com/sieben"
                        />
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Google-plus</label
                        >
                        <input
                          type="text"
                          v-model="profile.googlePlus"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. www.google-plus.com/sieben"
                        />
                      </div>
                    </div>

                    <div class="col-md-12">
                      <input
                        type="submit"
                        value="Save Socials"
                        class="btn btn-green btn-h-60 text-white min-width-px-210 rounded-5 text-uppercase"
                      />
                    </div>
                  </div>
                </fieldset>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- socials container Begins  -->

    <div class="container">
      <!-- socials container Begins -->
      <div class="mb-15 mb-lg-23">
        <div class="row">
          <div class="col-xxxl-9 px-lg-13 px-6">
            <h5 class="font-size-6 font-weight-semibold mb-11">Contact</h5>
            <div
              class="contact-form bg-white shadow-8 rounded-4 pl-sm-10 pl-4 pr-sm-11 pr-4 pt-15 pb-13"
            >
              <form @submit.prevent="onSubmit">
                <fieldset>
                  <div class="row mb-xl-1 mb-9">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="namedash"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Phone Number</label
                        >
                        <input
                          type="text"
                          class="form-control h-px-48"
                          id="namedash"
                          v-model="profile.phoneNumber"
                          placeholder="eg. +90 438 447 32 57"
                        />
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Email</label
                        >
                        <input
                          type="text"
                          v-model="profile.email"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. admin@sieben.com"
                        />
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Website</label
                        >
                        <input
                          type="url"
                          v-model="profile.website"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. www.siben.com"
                        />
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Country</label
                        >
                        <input
                          v-model="profile.country"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. Senegal"
                        />
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >City</label
                        >
                        <input
                          v-model="profile.city"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="e.g. Lagos"
                        />
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Latitude</label
                        >
                        <input
                          type="text"
                          v-model="profile.latitude"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. 41.1546574"
                        />
                      </div>
                    </div>
                    <div class="col-lg-12">
                      <div class="form-group">
                        <label
                          for="select2"
                          class="d-block text-black-2 font-size-4 font-weight-semibold mb-4"
                          >Longitude</label
                        >
                        <input
                          v-model="profile.longitude"
                          type="text"
                          class="form-control h-px-48"
                          id="namedash"
                          placeholder="eg. 21.3323455"
                        />
                      </div>
                    </div>

                    <div class="col-md-12">
                      <input
                        type="submit"
                        value="Save Contact"
                        class="btn btn-green btn-h-60 text-white min-width-px-210 rounded-5 text-uppercase"
                      />
                    </div>
                  </div>
                </fieldset>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- socials container Begins  -->
  </div>
</template>
<script>
import authMixin from "./../../mixins/auth";
import { validationMixin } from "vuelidate";
import { maxLength, minLength } from "vuelidate/lib/validators";
import authHeader from "./../../store/modules/authHeader";
import Axios from "axios";
export default {
  mixins: [validationMixin, authMixin],
  mounted() {
    Axios.get(process.env.VUE_APP_PORT + "/categories").then(result => {
      this.categories = result.data;
    });

    Axios.get(process.env.VUE_APP_PORT + "/user", {
      headers: authHeader()
    }).then(result => {
      this.profile = result.data.data;
      this.profile.industry = this.profile.industry.map(el => el._id)[0];
      this.profile.category = this.profile.category.map(el => el._id)[0];
    });
    Axios.get(process.env.VUE_APP_PORT + "/industries").then(result => {
      this.industries = result.data;
    });
  },
  data() {
    return {
      industries: [],
      categories: [],
      profile: {
        company_name: "",
        industry: "",
        category: "",
        employeeSize: "",
        yearCreated: "",
        about: "",
        twitter: "",
        linkedln: "",
        googlePlus: "",
        phoneNumber: "",
        website: "",
        city: "",
        longitude: ""
      }
    };
  },
  validations: {
    profile: {
      yearCreated: { maxLength: maxLength(4), minLength: minLength(4) }
    }
  },
  methods: {
    setCategory(category) {
      this.$emit("input", parseInt(category));
    },
    setIndustry(category) {
      this.$emit("input", parseInt(category));
    },
    validateState(profile) {
      const { $dirty, $error } = profile;
      return $dirty ? !$error : null;
    },
    onSubmit() {
      this.$v.profile.$touch();
      if (this.$v.profile.$anyError) {
        return;
      }
      console.log(this.profile);
      Axios.patch(process.env.VUE_APP_PORT + "/user/profile", this.profile, {
        headers: authHeader()
      })
        .then(() => {
          this.$toastr.s("Profile Updated successfully", "Success!");
        })
        .catch(error => {
          console.log(error.response);
          this.$toastr.e(error.response.data.message, "Error!");
        });
    }
  }
};
</script>
